import React, { MouseEvent, useCallback } from 'react';
import { NavLink } from 'react-router-dom';
import { useMainContext } from '../../Context/Main';
import Profile from '../Profile';
import {
  AdminIcon,
  ArrowIcon,
  CompanyIcon,
  Container,
  LogoutIcon,
  Nav,
  QuotesIcon,
  WrapperLink,
  WrapperProfile,
  WrapperText
} from './styles';

const Menu: React.FC = () => {
  const { menuOpen, setMenuOpen, profile } = useMainContext();

  const handleClickMenu = useCallback(
    (e: MouseEvent<HTMLElement>) => {
      document.body.classList.toggle('overflow-hidden', !menuOpen);
      setMenuOpen(!menuOpen);
    },
    [menuOpen, setMenuOpen]
  );
  // console.log('menu', !!profile);
  if (!profile) return <></>;
  return (
    <Container
      className={menuOpen ? 'menu-open' : ''}
      onClick={handleClickMenu}
    >
      <Nav className={menuOpen ? 'menu-open' : ''}>
        <ul>
          <li>
            <WrapperLink>
              <NavLink
                exact
                to="/registerSupplier"
                activeClassName="active"
                onClick={handleClickMenu}
              >
                <WrapperText>
                  <CompanyIcon />
                  <span>Cadastro Fornecedor</span>
                </WrapperText>
                <div>
                  <ArrowIcon />
                </div>
              </NavLink>
            </WrapperLink>
          </li>
          <li>
            <WrapperLink>
              <NavLink
                exact
                to="/"
                activeClassName="active"
                onClick={handleClickMenu}
              >
                <WrapperText>
                  <QuotesIcon />
                  <span>Cotações</span>
                </WrapperText>
                <ArrowIcon />
              </NavLink>
            </WrapperLink>
          </li>
          {profile?.isAdm && (
            <li>
              <WrapperLink className="admin">
                <NavLink
                  exact
                  to="/admin"
                  activeClassName="active"
                  onClick={handleClickMenu}
                >
                  <WrapperText>
                    <AdminIcon />
                    <span>Administrativo</span>
                  </WrapperText>
                  <ArrowIcon />
                </NavLink>
              </WrapperLink>
            </li>
          )}
          <li>
            <WrapperLink>
              <NavLink
                exact
                to="/logout"
                activeClassName="active"
                onClick={handleClickMenu}
              >
                <WrapperText>
                  <LogoutIcon />
                  <span>Sair</span>
                </WrapperText>
                <ArrowIcon />
              </NavLink>
            </WrapperLink>
          </li>
        </ul>
        <WrapperProfile>
          <Profile />
        </WrapperProfile>
      </Nav>
    </Container>
  );
};
export default React.memo(Menu);
