import { ISupplier } from '../Interfaces/Supplier';
import { mocaSupplier } from '../MoocasData/Supplier';

export const SupplierService = {
  Get: (cnpj: string) =>
    new Promise<ISupplier>(resolve =>
      setTimeout(() => {
        const result = mocaSupplier.find(s => s.cnpj === cnpj);
        resolve(result || ({} as ISupplier));
      }, 2000)
    ),

  Update: (item: ISupplier) =>
    new Promise<string>(resolve => {
      setTimeout(() => {
        // update na base
        resolve('done!');
      }, 2000);
    })
};
