import React from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import { isAuthenticated } from '../Auth';
import { ContextQuoteItemsProvider } from '../Context/QuoteItems';
import Admin from '../Pages/Admin';
import Login from '../Pages/Login';
import Logout from '../Pages/Logout';
import Quotes from '../Pages/Quotes';
import QuotesDetail from '../Pages/QuotesDetail';
import RegisterSupplier from '../Pages/RegisterSupplier';

const PrivateRoute: React.FC<{ path: string; exact?: boolean }> = ({
  children,
  path,
  exact
}) => (
  <Route exact path={path}>
    {isAuthenticated() ? <>{children}</> : <Redirect to="/login" />}
    {/* {isAuthenticated() ? <>{children}</> : <h1>tem que se logar</h1>} */}
  </Route>
);

const Routes: React.FC = () => (
  <Switch>
    <PrivateRoute exact path="/">
      <Quotes />
    </PrivateRoute>
    <PrivateRoute path="/quotesDetail/:quoteId">
      <ContextQuoteItemsProvider>
        <QuotesDetail />
      </ContextQuoteItemsProvider>
    </PrivateRoute>
    <PrivateRoute path="/registerSupplier">
      <RegisterSupplier />
    </PrivateRoute>
    <PrivateRoute path="/admin">
      <Admin />
    </PrivateRoute>
    <Route path="/logout">
      <Logout />
    </Route>
    <Route path="/login">
      <Login />
    </Route>
    <Route path="*">
      <h1>Página não encontrada</h1>
    </Route>
    {/* <PrivateRoute path="/app" component={() => Quotes} /> */}
  </Switch>
);

// const Routes: React.FC = () => <h1>rota</h1>;

export default Routes;
