import React from 'react';
import { IQuoteItem } from '../../Interfaces/QuotesDetail';
import { Container } from './styles';

interface Props extends React.HTMLAttributes<HTMLDivElement> {
  data: IQuoteItem[];
}
const QuoteItemsCsv: React.FC<Props> = ({ data, ...rest }) => {
  if (data.length === 0) return <></>;
  return (
    <Container {...{ rest }}>
      <table>
        <thead>
          <tr>
            <th>Item Id</th>
            <th>Código Produto</th>
            <th>Produto</th>
            <th>Possui Produto</th>
            <th>Valor Unitário</th>
            <th>Aliq. IPI(%)</th>
            <th>Aliq. ICMS(%)</th>
            <th>Prazo Entrega(Dias)</th>
            <th>Observação</th>
          </tr>
        </thead>
        <tbody>
          {data.map(d => (
            <tr key={d.itemId}>
              <td>{d.itemId}</td>
              <td>{d.productCode}</td>
              <td>{d.product}</td>
              <td>{d.hasItem ? 'sim' : 'não'}</td>
              <td>{d.unitValue}</td>
              <td>{d.ipiValue}</td>
              <td>{d.icmsValue}</td>
              <td>{d.deliveryTime}</td>
              <td>{d.observation}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </Container>
  );
};

export default QuoteItemsCsv;
