import { IProfile } from '../Interfaces/Profile';
import { mocaProfile } from '../MoocasData/Profile';

export const ProfileService = {
  Get: (googleId: string) =>
    new Promise<IProfile>(resolve =>
      setTimeout(() => {
        const result = mocaProfile.find(p => p.googleId === googleId);
        resolve(result || ({} as IProfile));
      }, 2000)
    ),

  insert: (item: IProfile) =>
    new Promise<string>(resolve => {
      setTimeout(() => {
        // update na base
        resolve('done!');
      }, 2000);
    })
};
