import React, { useCallback } from 'react';
import { SubmitHandler, useForm } from 'react-hook-form';
import { ISupplier } from '../../Interfaces/Supplier';
import { InputForm, Row, Column, Panel, Button } from '../../components';

import { Container } from './styles';
import { useMainContext } from '../../Context/Main';
import { SupplierService } from '../../services/supplierService';

const RegisterSupplier: React.FC = () => {
  const { setLoading } = useMainContext();

  const {
    register,
    handleSubmit,
    reset,
    formState: { errors }
  } = useForm<ISupplier>();

  const onSubmit: SubmitHandler<ISupplier> = useCallback(
    async (item: ISupplier) => {
      try {
        setLoading(true);
        const response = await SupplierService.Update(item);
        setLoading(false);
        // setCompany();
      } catch (err) {
        console.log('error in save QuotesItemsForm', err);
      }
    },
    [setLoading]
  );

  return (
    <Container>
      <h1>Cadastro do Fornecedor</h1>
      <Panel>
        <form onSubmit={handleSubmit(onSubmit)} noValidate>
          <Row>
            <Column>
              <InputForm
                label="Razão Social"
                register={register('companyName', {
                  required: { value: true, message: 'campo obrigatório' }
                })}
                placeholder="Digite a Razão Social"
                type="text"
                errors={errors}
              />
            </Column>
          </Row>
          <Row>
            <Column>
              <InputForm
                label="Nome Fantasia"
                register={register('fantasyName', {
                  required: { value: true, message: 'campo obrigatório' }
                })}
                placeholder="Digite o Nome Fantasia"
                type="text"
                errors={errors}
              />
            </Column>
          </Row>
          <Row>
            <Column>
              <InputForm
                label="CNPJ"
                register={register('fantasyName', {
                  required: { value: true, message: 'campo obrigatório' }
                })}
                placeholder="Digite o CNPJ"
                type="text"
                errors={errors}
                mask="cnpj"
              />
            </Column>
          </Row>
          <Row>
            <Column>
              <InputForm
                label="CEP"
                register={register('zipCode', {
                  required: { value: true, message: 'campo obrigatório' }
                })}
                placeholder="Digite o CEP"
                type="text"
                errors={errors}
                mask="cep"
              />
            </Column>
          </Row>
          <Row>
            <Column>
              <InputForm
                label="Endereço"
                register={register('address', {
                  required: { value: true, message: 'campo obrigatório' }
                })}
                placeholder="Digite o Endereço"
                type="text"
                errors={errors}
              />
            </Column>
            <Column width="50%">
              <InputForm
                label="Numero"
                register={register('address', {
                  required: { value: true, message: 'campo obrigatório' }
                })}
                placeholder="Digite o Endereço"
                type="number"
                errors={errors}
              />
            </Column>
          </Row>
          <Row>
            <Column width="50%">
              <InputForm
                label="Complemento"
                register={register('complement')}
                placeholder="Digite o Complemento"
                type="text"
                errors={errors}
              />
            </Column>
            <Column>
              <InputForm
                label="Bairro"
                register={register('neighborhood', {
                  required: { value: true, message: 'campo obrigatório' }
                })}
                placeholder="Digite o Bairro"
                type="text"
                errors={errors}
              />
            </Column>
          </Row>
          <Row>
            <Column width="50%">
              <InputForm
                label="UF"
                register={register('state', {
                  required: { value: true, message: 'campo obrigatório' }
                })}
                placeholder="Digite o Estado"
                type="text"
                errors={errors}
                max="2"
              />
            </Column>
            <Column>
              <InputForm
                label="Cidade"
                register={register('city', {
                  required: { value: true, message: 'campo obrigatório' }
                })}
                placeholder="Digite a Cidade"
                type="text"
                errors={errors}
              />
            </Column>
          </Row>
          <Row>
            <Column>
              <br />
              <Button type="submit">Salvar</Button>
            </Column>
          </Row>
        </form>
      </Panel>
    </Container>
  );
};

export default RegisterSupplier;
